import React, {Component} from 'react'
import {connect} from 'react-redux'
import { Input, Button } from 'antd';
import {actionCreators} from './store'
import {ContentWapper,ContentBox,FixBox,FixInputBox,FixOutputBox,FixBtn,TipsWapper} from './style'

class Punctuationpred extends Component{
    render(){
        const { puncFixResult,handlePuncFix } = this.props;
        return(
            <ContentWapper>
                <ContentBox>
                    <TipsWapper>
                        对中文语音文本进行智能切分，添加标点符号。
                    </TipsWapper>
                    <FixBox>
                        <FixInputBox>
                            <Input.TextArea allowClear ref={(input)=>{this.needFixText = input}} style={{ width: '100%' }} rows={15} placeholder="在此输入需要标点预测的语句" />
                        </FixInputBox>
                        <FixOutputBox>
                            <Input.TextArea disabled  value={puncFixResult} style={{ width: '100%' }} rows={15} placeholder="标点预测结果" />
                        </FixOutputBox>
                        <FixBtn>
                            <Button type="primary" onClick={()=>handlePuncFix(this.needFixText.state.value)}>标点预测</Button>
                        </FixBtn>
                    </FixBox>
                </ContentBox>
            </ContentWapper>
        )
    }
}

const mapStateToProps = (state)=>{
    return {
        puncFixResult: state.getIn(['punc','puncFixResult'])
    }
}

const mapDispatch = (dispatch) =>({
    handlePuncFix(text){
        dispatch(actionCreators.handlePuncFix(text))
    }
})

export default connect(mapStateToProps,mapDispatch)(Punctuationpred);